import Frame from '@/views/frame'
import Layout from "@/views/_Layout/Layout";



export const dynamicRoutes = {
  path: '/',
  name: 'Frame',
  component: Frame,
  // redirect: '/Login',
  children: [
    {path: '/', redirect: 'Home'},
    {
      path: '/Home',
      name: 'Home',
      component: () => import('../views/Home/Home'),
      meta: {
        title: ''
      }
    },
    {
      path: '/User',
      name: 'User',
      // redirect: 'User/UserList',
      component: Layout,
      meta: {
        title: ''
      },
      children: [
        {
          path: 'UserList',
          name: 'UserList',
          component: () => import('../views/User/UserList'),
          meta: {
            title: ''
          }
        },
        {
          path: 'UserInfo',
          name: 'UserInfo',
          component: () => import('@/views/User/UserInfo'),
          hidden: true,
          meta: {
            title: ''
          }
        },
        {
          path: 'LiveRate',
          name: 'LiveRate',
          component: () => import('@/views/User/LiveRate'),
          hidden: false,
          meta: {
            title: ''
          }
        },
      ]
    },
    {
      path: '/Statement',
      name: 'Statement',
      redirect: 'Statement/WechatRedPackage',
      component: Layout,
      meta: {
        title: ''
      },
      children: [
        {
          path: 'DailyStatisticalWrap',
          name: 'DailyStatisticalWrap',
          component: () => import('@/views/Statement/DailyStatisticalWrap'),
          meta: {
            title: ''
          }
        },
        {
          path: 'ChargeStatistics',
          name: 'ChargeStatistics',
          component: () => import('@/views/Statement/ChargeStatistics'),
          meta: {
            title: ''
          }
        },


      ]

    },
    {
      path: '/Channel',
      name: 'Channel',
      redirect: 'Channel/ChannelManage',
      component: Layout,
      meta: {
        title: ''
      },
      children: [
        {
          path: 'ChannelManage',
          name: 'ChannelManage',
          component: () => import('@/views/Channel/ChannelManage'),
          meta: {
            title: ''
          }
        },


      ]
    },
    {
      path: '/WechatModule',
      name: 'WechatModule',
      redirect: 'WechatModule/WechatJump',
      component: Layout,
      meta: {
        title: ''
      },
      children: [
        {
          path: 'WechatJump',
          name: 'WechatJump',
          component: () => import('@/views/Wechat/WechatJump'),
          meta: {
            title: 'H5'
          }
        },


      ]
    }, {
      path: '/Auth',
      name: 'Auth',
      redirect: 'Auth/OperatorList',
      component: Layout,
      meta: {
        title: ''
      },
      children: [
        {
          path: 'OperatorList',
          name: 'OperatorList',
          component: () => import('@/views/Auth/OperatorList'),
          meta: {
            title: ''
          }
        },
        {
          path: 'Role',
          name: 'Role',
          component: () => import('@/views/Auth/Role'),
          meta: {
            title: ''
          }
        },
        {
          path: 'RoleModify',
          name: 'RoleModify',
          component: () => import('@/views/Auth/RoleModify'),
          hidden: true,
          meta: {
            title: ''
          }
        },
        {
          path: 'Permission',
          name: 'Permission',
          component: () => import('@/views/Auth/Permission'),
          meta: {
            title: ''
          }
        },


      ]
    }
  ]
}



